.modal {
  width: 480px;
  display: flex;
  flex-wrap: wrap;
  margin-top: -260px;
  margin-left: -240px;
  background-color: #fff;
  border-radius: 8px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-sizing: border-box;
  z-index: 10;
  opacity: 1;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #fff;
    z-index: -1;
  }
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  background: #E5E5E5;
  opacity: .5;
  z-index: -5;
  width: 100%;
  height: 100%;
}

.modal h3 {
  font-size: 28px;
  line-height: 34px;
  margin: 0;
  margin-bottom: 16px;
  letter-spacing: -0.02em;
  text-transform: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  color: $blue;
}

.modal p {
  width: 359px;
  line-height: 170%;
  margin: 0;
  margin-bottom: 35px;
}

.modal__main-btn {
  display: block;
  width: 264px;
  margin: 0 0 16px 61px;
  padding: 18px 0 19px 0;
  border: 1px solid transparent;
  border-radius: 80px;
  text-align: center;
  background: $gradient;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  font-variant: small-caps;
  color: #fff;
}

.modal__close {
  position: absolute;
  width: 18px;
  height: 17px;
  top: 32px;
  right: 32px;
  border: 0;
  font-size: 0;
  cursor: pointer;
  background: url("../img/close-btn.svg")
}

@media (max-width: $mobile-width-max) {
  .modal__container {
    width: 320px;
    margin: 0 auto;
  }

  .callback-modal__form {
    margin: 0 auto 16px;
    padding-top: 20px;
  }

  .modal h3 {
    margin-bottom: 18px;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
  }

  .modal__close {
    top: 24px;
    right: 24px;
  }

  .modal p {
    width: 287px;
    margin: 0 auto 30px;
    font-size: 14px;
    line-height: 160%;
    text-align: center;
    box-sizing: border-box;
  }

  .modal__main-btn {
    width: 264px;
    margin: 16px auto;
    display: block;
  }
}
